<template>
    <div class="top">
        <div class="logo">

        </div>
        <div class="search">
            <el-input
                placeholder="请搜索你感兴趣的内容..."
                class="searchBox"
                v-on:focus="longer"
                v-on:blur="shorter"
                v-bind:class="{longerAnimation:isFocus,shorterAnimation:!isFocus&&start}"
                ref="searchBox"
                >
                <i slot="suffix" class="el-input__icon el-icon-search" @click="showMsg"></i>
            </el-input>
        </div>
        <div class="user">

        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            isFocus: false,
            start: false
        }
    },
    methods: {
        showMsg(){
            console.log('666')
        },
        longer(){
            this.isFocus = true
            this.start = true
        },
        shorter(){
            this.isFocus = false
        }
    }
}
</script>

<style scoped>

@keyframes longer{
    0% {
        width: 80%;
    }
    100%{
        width: 100%;
    }
}
@keyframes shorter {
    0% {
        width: 100%;
    }
    100%{
        width: 80%;
    }
}
.top{
    width: 100%;
}
.search{
    width: 40%;
}
.searchBox{
    width: 80%;
}
.longerAnimation{
    animation: longer 1s forwards;
}
.shorterAnimation{
    animation: shorter 1s forwards;
}
.searchBox i:hover{
    cursor: pointer;
}
</style>





